import React, {useState, useEffect} from 'react';
import {getIsLoadingSelector, getListSelector} from "../selectors";
import {Link} from "react-router-dom";
import { useSelector } from 'react-redux';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import Loader from "react-loader-spinner";


const List = () => {
  const isLoading = useSelector(getIsLoadingSelector);
  const alphabet = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""];

  const list = useSelector(getListSelector);
  const [selectedLetter, setSelectedLetter] = useState("");
  const [filteredList, setFilteredList] = useState([]);
  const [fio, setFio] = useState('');
  const [showLetter, setShowLetter] = useState(true);


  const onFioChange = (event) => {
    const printedValue = event.target.value;

    setFio(printedValue);
    if (printedValue.length > 0) {
      setShowLetter(false);
      setFilteredList(list.filter((item) => {
        return item.name.toLowerCase().startsWith(printedValue.toLowerCase());
      }));
    } else {
      setShowLetter(true);
      onLetterClick(selectedLetter);
    }

  };

  const onLetterClick = (letter) => {
    setSelectedLetter(letter);
    setFio('');
    setFilteredList(list.filter((item) => {
      return item.name[0].toLowerCase() === letter.toLowerCase();
    }));
  };

  useEffect(() => {
    onLetterClick('');
  }, [list]);

  if (isLoading) return <Loader
    style={{textAlign: "center", padding: "3rem"}}
    type="TailSpin"
    color="#254986"
    height={100}
    width={100}
  />;

  return (
    <div style={{minHeight: "700px"}}>
      <ul className="pt-3 pagination pagination-sm justify-content-center flex-wrap">
        {alphabet.map(letter => (
          <li className="page-item" key={letter}>
            <a href="#" className="page-link p-1" onClick={() => onLetterClick(letter)}>{letter}</a>
          </li>
        ))}
      </ul>

      <div className="input-group">
        <input type="text" className="form-control" value={fio} onChange={onFioChange} placeholder=" "/>
      </div>

      <h1   className={`pt-3 ${!showLetter ? ' invisible' : ''}`}>{selectedLetter}</h1>

      <TransitionGroup component="ul" className="mb-3 list-unstyled card-columns">
        {filteredList.map(item => (
          <CSSTransition key={item.guid} timeout={500} classNames="item">
            <li key={item.guid}>
              <Link to={`/info/${item.guid}`}>{item.name}</Link>
            </li>
          </CSSTransition>
        ))}
      </TransitionGroup>

    </div>
  )
};

export default List;
