import React, {useEffect} from 'react';
import {Operation} from "../reducer";
import {getItemSelector, getIsLoadingSelector} from "../selectors";
import {Link} from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux'
import Loader from "react-loader-spinner";

const Item = (props) => {

  const dispatch = useDispatch();
  const item = useSelector(getItemSelector);
  const isLoading = useSelector(getIsLoadingSelector);

  useEffect(() => {
    dispatch(Operation.loadItem(props.match.params.id));
  }, []);


  const {name, academic_title, academic_degree, position, institute, department, professional_training, publications, publication_activity} = item;


  if (isLoading) return <Loader
    style={{textAlign: "center", padding: "3rem"}}
    type="TailSpin"
    color="#254986"
    height={100}
    width={100}
  />;


  return (
    <div className="row pt-3">
      <div className="col-md-9">
        <h4>{name}</h4>
        <h6></h6>
        <p>{position}</p>

        <h6></h6>
        <p>{academic_degree}</p>

        <h6> / :</h6>
        <p>{institute}</p>

        <h6></h6>
        <p>{department}</p>

        <h6> </h6>
        <p dangerouslySetInnerHTML={{__html: professional_training}}></p>

        <h6>  :</h6>
        <p dangerouslySetInnerHTML={{__html: publications}}></p>

        <h6>         ()</h6>
        <p dangerouslySetInnerHTML={{__html: publication_activity}}></p>
      </div>
      <div className="col-md-3">
        <div className="card mb-3">

          <img src="https://www.ncfu.ru/export/uploads/Izobrajeniya-Sotrudniku/Avdeev_E.A..jpg?__scale=w:321,h:481" className="card-img-top img-fluid"/>

          <div className="card-body text-secondary">
            <h5 className="card-title">{name}</h5>
            <div className="card-text">
              <p><strong>:</strong></p>
              <p><strong>:</strong></p>
              <p><strong>E-mail:</strong></p>
            </div>
          </div>
        </div>

      </div>

      <Link to={`/`}>  </Link>

    </div>
  )
};

export default Item;
